.container {
  display: flex;
  flex-direction: column;
  min-height: calc(100vh - 3.5em);
  justify-content: space-between;
  background-position: center !important;
  background-size: cover !important;
  background-color: black;
}

.content {
  align-items: center;
  justify-content: center;
  text-align: center;
}
.container > div {
  padding: 0.2em 0.25em;
}

.professionalHeadshot {
  height: 12em;
  width: 12em;
  border-radius: 50%;
  border: 3px solid #fff;
}

.title {
  font-weight: 600;
  font-size: 2.5em;
  text-shadow: 2px 4px 3px rgb(0 0 0 / 30%);
  color: white;
}

.about {
  color: white;
  font-weight: 200;
  font-size: 21px;
  word-spacing: 4px;
}

.socials {
  margin-top: 1em;
}

.socialsIcon {
  color: white;
  font-size: 2em;
  border-radius: 0.125em;
  margin: 0 0.25em;
  opacity: 0.4;
}

.socialsIcon:hover {
  opacity: 0.7;
}

.resumeButton {
  margin-top: 1em;
  opacity: 0.8;
  background: transparent;
  border: 2px solid white;
  color: white;
  font-weight: 600;
  font-size: 21px;
  padding: 0.5em 1em;
  border-radius: 0.125em;
  cursor: pointer;
  transition: 0.2s;
}

.resumeButton:hover,
.resumeButton:active {
  background: white !important;
  border: 2px solid white !important;
  color: black !important;
}

.resumeIcon {
  font-size: 1.25em;
}

.location {
  color: white;
  opacity: 0.1;
  font-weight: 700;
  font-size: 2em;
  word-spacing: 4px;
  padding: 0 0 10px 20px;
}
